import React from 'react';
const Todolist = (props) => {



    return (
        <>
            <div className="todo_list">
                <button
                    className="btn cross"
                    onClick={() => props.onSelect(props.id)}
                ><i class="fa fa-close"></i></button>
                <li className="text">{props.text}</li>

            </div>
        </>
    );
}
export default Todolist;




