import React, { useState } from 'react';
import './App.css';
import Todolist from './components/Todolist';

const App = () => {


	const [InputList, setInputList] = useState("");
	const [Items, setItems] = useState([]);
	const itemEvent = (e) => {
		setInputList(e.target.value);
	}
	const listofitems = () => {
	if(InputList.length !== 0)	{setItems((old) => {
			return [...old, InputList];
		})
		setInputList("");}
		else{
			alert("please enter something")
		}
	}
	const deleteItems = (id) => {
		setItems((old) => {
			return old.filter((arrele, index) => {
				return index !== id;
			})
		})
	}
	return (
		<div className="App">
			<header className="App-header">
				<div className="box">
					<div className="heading">
						<center><h2 >To-do List</h2></center>
					</div>
					<div className="buttom">


						<div className="group">
							<input type="text"
								required
								placeholder="Add a items.."
								value={InputList}
								onChange={itemEvent}

							/>
							<a href="#">
								<span className=" b1 glyphicon glyphicon-plus-sign"  onClick={listofitems}></span>
							</a>



						</div>

						<div className="list">
							<ul>
								{Items.map((val, index) => {
									return <Todolist key={index}
										id={index}
										text={val}
										onSelect={deleteItems} />
								})}

							</ul>

						</div>


					</div>
				</div>
			</header>

		</div>
	);

}

export default App;
